<template>
  <v-card>
    <v-card-title class="title pa-0" :class="color+'--text'">
      <v-avatar tile size="50">
        <v-icon medium :color="color">{{item.icon}}</v-icon>
      </v-avatar>
      <span>{{ item.disease }}</span>
      <v-spacer></v-spacer>
      <slot></slot>
    </v-card-title>
    <v-divider></v-divider>
    <v-subheader :class="color+' lighten-5'">{{ item.sub }}</v-subheader>
    <v-divider></v-divider>
    <v-card-text style="text-align: justify;" v-html="item.title"></v-card-text>
  </v-card>
</template>

<script>
export default {
  name: 'DiseaseDialog',
  props: ['color', 'item'],
  data () {
    return {}
  }
}
</script>



