<template>
  <v-card id="SymptomsDialogCard">
    <v-card-media src="/static/symptoms/symptoms.jpg" height="300px" class="white--text"></v-card-media>
    <v-card-title>
      <div>
        <div class="headline">{{ symptom.name }}</div>
        <span class="grey--text">Περιγραφή συμπτώματος</span>
      </div>
    </v-card-title>
    <!-- <v-card-title class="title pa-0" :class="color+'--text'">
      <v-avatar tile size="50">
        <v-icon medium :color="color">visibility</v-icon>
      </v-avatar>
      <span>{{ symptom.name }}</span>
      <v-spacer></v-spacer>
      <slot name="topButton"></slot>
    </v-card-title> -->
    <!-- <v-divider></v-divider>
    <v-subheader :class="color+' lighten-5'">Περιγραφή συμπτώματος</v-subheader> -->
    <v-divider></v-divider>
    <v-card-text class="subheading pa-0">
      <div class="pa-3">{{ symptom.description }}</div>
      <v-list v-if="symptom.types">
        <v-divider></v-divider>
        <v-subheader :class="color+' lighten-5'">Ενδεικτικοί τύποι συμπτώματος</v-subheader>
        <v-divider></v-divider>
        <template v-for="(type, t) in symptom.types">
          <v-list-tile :key="`tile-${t}`">
            <v-list-tile-avatar>
              <v-icon>format_list_bulleted</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>{{ type }}</v-list-tile-content>
          </v-list-tile>
          <v-divider v-if="t < symptom.types.length-1" inset :key="`divider-${t}`"></v-divider>
        </template>
      </v-list>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <slot></slot>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'SymptomsDialogCard',
  props: ['color', 'symptom'],
  data () {
    return {
    }
  }
}
</script>

<style lang="css">
.fwn{
  font-weight: normal;
}
.mediaOverlay{
  background-color: rgba(0,0,0,0.3);
}
</style>



