<template>
  <div id="EditPlantPage" class="pa-0">
    <transition-group name="scale-transition">
      <v-card key="PlantCard" v-if="plant">
        <v-card-title class="title white--text pa-0" :class="plant.vue_color || 'secondary'">
          <v-avatar tile color="white" size="55" class="ml-0 mr-2">
            <v-icon medium :color="plant.vue_color" class="mx-2">edit</v-icon>
          </v-avatar>
          <span class="mr-3">{{ chosenPlant }}</span>
          <v-spacer></v-spacer>
          <v-tooltip class="ma-0" left>
            <v-btn slot="activator" dark flat icon @click="logout">
              <v-icon medium>exit_to_app</v-icon>
            </v-btn>
            <span>Αποσύνδεση</span>
          </v-tooltip>
        </v-card-title>
        <v-card-text class="pa-0">
          <v-list two-line class="pa-0">
            <template v-for="(field, f) in fields">
              <v-divider :key="`topDivider-${f}`"></v-divider>
              <v-subheader :key="`subheader-${f}`" :class="plant.vue_color+' lighten-5'">{{ field.subHeader }}</v-subheader>
              <v-divider :key="`bottomDivider-${f}`"></v-divider>
              <v-list-tile :key="`tile-${f}`">
                <v-list-tile-content>
                  <v-text-field
                    :color="plant.vue_color"
                    :hint="field.hint"
                    v-model="plant[field.model]"
                    :loading="saving"
                    placeholder="placeholder"
                    :prepend-icon="field.icon"
                    :rules="field.required ? notEmptyRules:[]"
                    :required="field.required"></v-text-field>
                </v-list-tile-content>
              </v-list-tile>
            </template>
            <template v-if="plant.diseases.length > 0">
              <v-divider></v-divider>
              <v-subheader :class="plant.vue_color+' lighten-5'">Ασθένειες</v-subheader>
              <v-divider></v-divider>
              <v-list-tile v-for="(disease, d) in plant.diseases" :key="`tile_${d}`">
                <v-list-tile-content>
                  <v-text-field
                    :color="plant.vue_color"
                    label="Ονομασία ασθένειας"
                    :placeholder="disease.name"
                    hint="Διορθώστε το όνομα της ασθένειας"
                    v-model="plant.diseases[d].name"
                    prepend-icon="local_hospital"
                    :rules="notEmptyRules"></v-text-field>
                </v-list-tile-content>
                <v-list-tile-action>
                  <v-btn icon flat @click="removeDisease(d)"><v-icon>delete</v-icon></v-btn>
                </v-list-tile-action>
              </v-list-tile>
            </template>
            <v-divider></v-divider>
            <v-subheader :class="plant.vue_color+' lighten-5'">
              <span>Προσθέστε νέα ασθένεια</span>
              <v-spacer></v-spacer>
              <v-btn @click="resetNewDisease" icon flat><v-icon>refresh</v-icon></v-btn>
            </v-subheader>
            <v-divider></v-divider>
            <v-form ref="newDisease" blah>
              <v-list-tile v-for="(diseaseField, df) in diseaseFields" :key="`newDiseaseTile_${df}`">
                <v-list-tile-content>
                  <v-text-field
                    :color="plant.vue_color"
                    :label="diseaseField.label"
                    :prepend-icon="diseaseField.icon"
                    v-model="newDisease[diseaseField.model]"
                    :rules="notEmptyRules"></v-text-field>
                </v-list-tile-content>
              </v-list-tile>
              <v-subheader class="blue-grey lighten-5">Τύπος ασθένειας</v-subheader>
              <v-list-tile>
                <v-list-tile-sub-title>
                  <v-radio-group prepend-icon="title" v-model="newDisease.parasitic" row>
                    <v-radio mandatory :color="plant.vue_color" label="Παρασιτική" :value="true"></v-radio>
                    <v-radio :color="plant.vue_color" label="Μη παρασιτική" :value="false"></v-radio>
                  </v-radio-group>
                </v-list-tile-sub-title>
              </v-list-tile>
              <v-subheader class="blue-grey lighten-5">Μέτρα καταπολέμησης</v-subheader>
              <v-list-tile class="pb-1">
                <v-select
                  @input="addTreatment"
                  single-line
                  v-model="newDisease.treatment"
                  label="Καταπολέμηση ασθένειας"
                  hint="Πληκτρολογήστε το μέτρο που επιθυμείτε και πατήστε Enter"
                  chips
                  prepend-icon="security"
                  :color="plant.vue_color"
                  tags></v-select>
              </v-list-tile>
              <v-subheader class="blue-grey lighten-5">Το φυτό παρουσιάζει</v-subheader>
              <v-list-tile>
                <v-select
                  hint="Πληκτρολογήστε το σύμπτωμα που επιθυμείτε σε αιτιατική κλίση και πατήστε Enter"
                  @input="addSymptom"
                  single-line
                  v-model="diseaseSymptoms"
                  label="Συμπτώματα ασθένειας"
                  chips
                  prepend-icon="visibility"
                  :color="plant.vue_color"
                  tags></v-select>
              </v-list-tile>
              <!-- <template v-for="(symptom, s) in newDisease.rules">
                <v-subheader :key="`symptomSubheader_${s}`" class="blue-grey lighten-5">Κριτήρια ασθένειας για {{ symptom }}</v-subheader>
                <template v-for="(rule, r) in newDisease.rules[symptom]">
                  <v-list-tile  :key="`ruleTile_${s}_${r}`">
                    <v-list-tile-content>
                      <v-text-field :color="plant.vue_color" label="Πρόταση" v-model="rule.rule"></v-text-field>
                    </v-list-tile-content>
                    <v-list-tile-action v-if="diseaseRules.length > 1">
                      <v-btn icon flat @click="removeRule(r)"><v-icon>delete</v-icon></v-btn>
                    </v-list-tile-action>
                  </v-list-tile>
                  <v-list-tile :key="`valuesTile_${s}_${r}`">
                    <v-list-tile-content>
                      <v-select :color="plant.vue_color" tags v-model="rule.values" label="Τιμές"></v-select>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-btn v-if="r+1 == diseaseRules.length" icon flat @click="addRule"><v-icon>add</v-icon></v-btn>
                    </v-list-tile-action>
                  </v-list-tile>
                </template>
              </template> -->
            </v-form>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-btn flat fab color="green darken-1" @click="save" :loading="saving"><v-icon medium>save</v-icon></v-btn>
          <v-spacer></v-spacer>
          <v-btn flat fab color="red darken-1" @click="removePlant()" :loading="deleting"><v-icon medium>delete</v-icon></v-btn>
        </v-card-actions>
      </v-card>
      <v-card key="ErrorCard" v-else class="my-4">
        <v-card-title class="title white--text pa-0 secondary">
          <v-avatar tile color="white" size="55" class="mr-3">
            <v-icon medium>error</v-icon>
          </v-avatar>
          <span>Σφάλμα αναζήτησης</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="pa-0">
          <v-alert type="error" class="ma-0 pa-4 subheading" :value="true">
            Το κηπευτικό <b>{{ $route.params.gr }}</b> δεν βρέθηκε. Διορθώστε τη διεύθυνση <b>URL</b> ή μεταβείτε στη σελίδα <router-link class="white--text" to="/diagnosis">Διάγνωσης</router-link> για πλήρη κατάλογο των κηπευτικών του iGarden.
          </v-alert>
        </v-card-text>
      </v-card>
    </transition-group>
  </div>
</template>

<script>
import API from '@/lib/API'
import swal from 'sweetalert2'
import _ from 'lodash'
export default {
  name: 'EditPlantPage',
  props: ['chosenPlant', 'retrievedPlants'],
  data () {
    return {
      plant: null,
      plants: this.retrievedPlants,
      newDisease: {
        rules: {}
      },
      deleting: false,
      saving: false,
      notEmptyRules: [
        v => !!v || 'Απαιτούμενο πεδίο!'
      ],
      diseaseSymptoms: [],
      diseaseRules: [{
        rule: '',
        values: []
      }],
      diseaseFields: [{
        label: 'Ονομασία ασθένειας',
        icon: 'info',
        model: 'name'
      }, {
        label: 'Παθογόνο ασθένειας',
        icon: 'bug_report',
        model: 'pathogen'
      }, {
        label: 'Περιγραφή ασθένειας',
        icon: 'description',
        model: 'description'
      }]
    }
  },
  created () {
    this.plant = this.plants.find(p => p.gr === this.chosenPlant)
  },
  mounted () {},
  computed: {
    printDate: function () {
      var date = new Date()
      return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`
    },
    fields () {
      return [{
        subHeader: 'Κοινή ονομασία',
        hint: `Διορθώστε την κοινή ονομασία του φυτού ${this.plant.gr}.`,
        placeholder: this.plant.gr,
        model: 'gr',
        icon: 'local_florist',
        required: true
      }, {
        subHeader: 'Επιστημονική ονομασία',
        hint: `Διορθώστε την επιστημονική ονομασία του φυτού ${this.plant.gr}.`,
        placeholder: this.plant.latin,
        model: 'latin',
        icon: 'school',
        required: true
      }]
    },
    PLANT_ID () { return this.plants.find(p => p.gr === this.plant.gr)._id.$oid }
  },
  methods: {
    removePlant () {
      swal({
        type: 'warning',
        title: 'Προσοχή!',
        html: `Πρόκειται να διαγράψετε το φυτό <b>${this.plant.gr}</b> οριστικά!<br>Θέλετε σίγουρα να προχωρήσετε;`,
        showCancelButton: true,
        confirmButtonText: 'Διαγραφή',
        confirmButtonColor: 'red',
        cancelButtonText: 'Άκυρο'
      }).then(willDelete => {
        this.deleting = true
        if (willDelete.value) {
          API.deletePlant(this.plant._id.$oid).then(deletedPlant => {
            this.deleting = false
            swal({
              type: 'success',
              title: 'Επιτυχής διαγραφή!',
              html: `Το φυτό <b>${deletedPlant.gr}</b> αφαιρέθηκε επιτυχώς από το αρχείο! Πατώντας <b>ΟΚ</b> θα μεταφερθείτε αυτόματα στη σελίδα διάγνωσης.`
            }).then(ok => this.$router.replace('/diagnosis'))
          })
        } else { this.deleting = false }
      })
    },
    addRule () {
      this.diseaseRules.push({
        rule: '',
        values: []
      })
    },
    removeDisease (index) {
      this.plant.diseases.splice(index, 1)
    },
    removeRule (index) {
      this.diseaseRules.splice(index, 1)
    },
    addTreatment () {},
    addSymptom () {},
    resetNewDisease () { this.$refs.newDisease.reset() },
    save () {
      this.saving = true
      this.diseaseSymptoms.forEach(symptom => { this.newDisease.rules[symptom] = {} })
      if (_.keys(this.newDisease).length >= 6) { this.plant.diseases.push(this.newDisease) }
      if (this.plant.gr.trim().length === 0 || this.plant.latin.trim().length === 0) {
        this.saving = false
        return swal({
          type: 'error',
          title: 'Κενά πεδία!',
          text: 'Δεν επιτρέπεται η χρήση κενών πεδίων...'
        })
      }
      API.updatePlant(this.PLANT_ID, this.plant).then(plant => {
        this.saving = false
        swal({
          type: 'success',
          title: 'Επιτυχής αποθήκευση!',
          html: `Το φυτό ${plant.gr} ανανεώθηκε επιτυχώς!`
        }).then(ok => this.$router.push('/diagnosis'))
      })
    },
    logout () {
      return swal({
        type: 'warning',
        text: 'Θέλετε σίγουρα να αποσυνδεθείτε;',
        showCancelButton: true,
        confirmButtonText: ['Έξοδος'],
        cancelButtonText: ['Όχι'],
        confirmButtonColor: '#E53935'
      }).then(ok => {
        if (ok.value) {
          sessionStorage.removeItem('admin')
          this.$emit('logout')
          this.$router.go()
        }
      })
    }
  }
}
</script>

<style lang="css">
.whitePanel{
  background-color: rgba(255,255,255,0.55);
}
</style>



