<template>
  <CardLayout icon="home" title="Αρχική" url="/">
    <template slot="content">
      <v-card raised class="my-4">
        <v-card-text class="subheading pa-0">
          <v-carousel lazy class="elevation-0">
            <v-carousel-item
              v-for="(slide, s) in slides"
              :src="`/static/carousel/carousel_${s+1}.jpg`"
              :key="s"
              reverse-transition="scale-transition">
            </v-carousel-item>
          </v-carousel>
          <v-divider></v-divider>
          <v-container fluid fill-height class="pa-0">
            <v-layout fill-height row wrap>
              <v-flex v-for="(item, i) in items" sm12 md4 :key="i" fill-height>
                <v-card tile flat hover height="210">
                  <v-card-title class="justify-center light-green">
                    <v-icon dark x-large>{{ item.icon }}</v-icon>
                  </v-card-title>
                  <v-card-text class="pa-0">
                    <div class="lime accent-4 py-2 subheading text-xs-center">{{ item.title }}</div>
                    <p class="body-2 text-xs-center py-1 px-3">{{ item.text }}</p>
                  </v-card-text>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
      </v-card>
    </template>
  </CardLayout>
</template>

<script>
import CardLayout from '@/components/layouts/CardLayout'
export default {
  name: 'Landing',
  components: {
    CardLayout
  },
  data () {
    return {
      dialog: false,
      icon: 'home',
      slides: 5,
      items: [{
        icon: 'school',
        title: 'Πτυχιακή διατριβή',
        text: 'Εκπόνηση πτυχιακής διατριβής του φοιτητή Χρήστος Χάρης, του τμήματος Βιοτεχνολογίας, του ΓΠΑ.'
      }, {
        icon: 'library_books',
        title: 'Διάγνωση',
        text: 'Μπορείτε να ακολουθήστε το διαγνωστικό οδηγό για τα κηπευτικά που έχουν εισαχθεί στη βάση γνώσεως.'
      }, {
        icon: 'edit',
        title: 'Επεξεργασία',
        text: 'Μπορείτε να επεξεργαστείτε ολοκληρωτικά τη βάση γνώσεως.'
      }]
    }
  },
  mounted () {
  },
  methods: {
    agree: (text) => alert(text),
    disagree: text => alert(text),
    diagnose (plant) {
      this.$router.push(`/diagnosis/${plant}`)
    }
  }
}
</script>

<style>
.whitePanel{
  background-color: rgba(255,255,255,0.55);
}
.flex-title{
  background-color: rgba(0,0,0,0.75);
  cursor: pointer;
}
.bdrd{
  border-radius: 8px;
}
.itemTitle{
  font-weight: normal;
  font-size: 17px;
}
.itemText{
  text-align: justify;
}
</style>



