<template>
  <div class="pa-0 my-4" id="AdminEdit">
    <div v-if="chosenPlant">
      <edit-plant-page v-if="plants.length > 0" :retrievedPlants="plants" :chosenPlant="chosenPlant"></edit-plant-page>
      <loading-plants :plant="chosenPlant" v-else></loading-plants>
    </div>
    <v-card v-else class="transparent">
      <v-card-title class="pa-0">
        <v-toolbar flat tabs color="light-green">
          <v-tooltip class="ma-0" right>
            <v-btn slot="activator" dark flat large icon>
              <v-icon medium>verified_user</v-icon>
            </v-btn>
            <span>Έχετε αποκτήσει δικαιώματα διαχειριστή</span>
          </v-tooltip>
          <v-spacer></v-spacer>
          <v-tooltip class="ma-0" left>
            <v-btn slot="activator" dark flat large icon @click="logout">
              <v-icon medium>exit_to_app</v-icon>
            </v-btn>
            <span>Αποσύνδεση</span>
          </v-tooltip>
          <v-tabs slot="extension" v-model="tabs" color="light-green" grow>
            <v-tabs-slider color="white"></v-tabs-slider>
            <v-tab :href="`#${action.icon}`" v-for="(action, i) in actions" :key="i">
              <v-icon dark large>{{ action.icon }}</v-icon>
            </v-tab>
          </v-tabs>
        </v-toolbar>
      </v-card-title>
      <v-card-text class="pa-0">
        <v-slide-y-transition>
          <v-tabs-items key="tabs-items" class="my-0" v-if="plants.length !== 0" v-model="tabs">
            <v-tab-item :id="action.icon" v-for="(action, i) in actions" :key="i">
              <component
                :subHeader="action.subHeader"
                :retrievedPlants="plants"
                @updatedPlants="plants = $event"
                :is="action.component"
                class="pa-0">
              </component>
            </v-tab-item>
          </v-tabs-items>
        </v-slide-y-transition>
        <loading-plants v-if="plants.length == 0"></loading-plants>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import AddPlant from './AddPlant'
import UpdateLibrary from './UpdateLibrary'
import DeletePlant from './DeletePlant'
import EditPlantPage from './EditPlantPage'
import LoadingPlants from '@/components/LoadingPlants'
import API from '@/lib/API'
import swal from 'sweetalert2'
export default {
  name: 'Edit',
  components: {
    AddPlant,
    UpdateLibrary,
    DeletePlant,
    'edit-plant-page': EditPlantPage,
    'loading-plants': LoadingPlants
  },
  data () {
    return {
      chosenPlant: this.$route.params.gr,
      title: 'Επεξεργασία Αρχείου',
      tabs: null,
      plants: [],
      username: '',
      password: '',
      actions: [{
        subHeader: 'Προσθήκη κηπευτικού',
        icon: 'add',
        component: 'AddPlant'
      }, {
        subHeader: 'Επεξεργασία συμπτωμάτων',
        icon: 'visibility',
        component: 'UpdateLibrary'
      }, {
        subHeader: 'Διαγραφή κηπευτικών',
        icon: 'delete',
        component: 'DeletePlant'
      }]
    }
  },
  mounted () {
    API.getPlants().then(plants => { this.plants = plants })
  },
  methods: {
    logout () {
      return swal({
        type: 'warning',
        text: 'Θέλετε σίγουρα να αποσυνδεθείτε;',
        showCancelButton: true,
        confirmButtonText: ['Έξοδος'],
        cancelButtonText: ['Όχι'],
        confirmButtonColor: '#E53935'
      }).then(ok => {
        if (ok.value) {
          sessionStorage.removeItem('admin')
          this.$emit('logout')
        }
      })
    }
  }
}
</script>



